export default {
	data() {
		return {
			page:1,
			historyInfo:{},
			historyList:[],
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '- 暂时没有新内容了呢 -'
			},
			status: 'loading',
			code:'',
			title:'数字元素测试',
			collectsId: '',
		};
	},
	onLoad(options){
		if(options.title){
			this.title = options.title
			this.collectsId = options.collectsId
		}
	},
	onShow(){
		this.page = 1;
		this.historyInfo = {};
		this.historyList = []
		this.getExaminationHistory()
	},
	onPullDownRefresh() {
		this.page = 1;
		this.historyInfo = {};
		this.historyList = []
		uni.showLoading({
			title: '加载中'
		});
		this.getExaminationHistory()
		uni.hideLoading();
		uni.stopPullDownRefresh()
	},
	onReachBottom(){
		if (this.code != '-116') {
			this.page = this.page + 1;
			this.getExaminationHistory();
		}
	},
	methods:{
		// 获取历史训练数据
		getExaminationHistory(){
			if(uni.getStorageSync('userInfo')){
				let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
				this.$Request.get('/appExaminationController.do?getExaminationHistory',{
					page:this.page,
					memberId:memberId,
					collectsId: this.collectsId
				}).then(res => {
					this.code = res.code
					this.historyInfo = res.data
					this.status = 'noMore'
					if(res.code == 0){
						this.historyList =  [...this.historyList, ...res.data.examList]
					}else if(res.code == '-118' || res.code == '-116'){
						this.status = 'noMore'
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showModal({
				    title: '提示',
				    content: '您尚未登录，是否去登录？',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:'/pages/loginAll/login'
				            })
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		},
	}
}
